<template>
  <div class="dashboard">
    <h1 class="subheading grey--text">Materijali</h1>

    <v-container class="my-5">

      <v-layout row justify-start class="mb-3">
        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('title')" slot="activator">
            <v-icon small left>folder</v-icon>
            <span class="caption text-lowercase">by name</span>
          </v-btn>
          <span>Sort by material name</span>
        </v-tooltip>
        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortByKolicina()" slot="activator">
            <v-icon small left>person</v-icon>
            <span class="caption text-lowercase">By kolicina</span>
          </v-btn>
          <span>Sort by preostala kolicina</span>
        </v-tooltip>
        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('sifraProizvoda')" slot="activator">
            <v-icon small left>folder</v-icon>
            <span class="caption text-lowercase">by sifraProizvoda</span>
          </v-btn>
          <span>Sort by sifraProizvoda </span>
        </v-tooltip>
      </v-layout>

      <v-card flat v-for="project in projects" :key="project.title">
        <v-layout row wrap :class="`pa-3 project ${project.status}`">
          <v-flex xs8 md4>
            <div class="caption grey--text">Material name</div>
            <div>{{ project.title }}</div>
          </v-flex>
          <v-flex xs2 sm2 md1>
            <div class="caption grey--text">sifraProizvoda</div>
            <div>{{ project.sifraProizvoda }}</div>
          </v-flex>
          <v-flex xs2 sm2 md1>
            <div class="caption grey--text">Kolicina</div>
            <div>{{ project.kolicina }} m</div>
          </v-flex>
          <v-flex xs4 sm2 md2>
            <div class="caption grey--text">Person</div>
            <div>{{ project.person }}</div>
          </v-flex>
          <v-flex xs4 sm2 md2>
            <div class="caption grey--text">Added on</div>
            <div>{{ project.due }}</div>
          </v-flex>
          <v-flex xs2 sm2 md2>
            <div class="right">
              <v-chip small :class="`${project.status} white--text my-2 caption`">{{ project.status }}</v-chip>
            </div>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
      </v-card>

    </v-container>

  </div>
</template>

<script>
import db from '@/fb'

export default {
  data() {
    return {
      projects: []
    }
  },
  methods: {
    sortBy(prop) {
      this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
    },
    sortByKolicina(prop='kolicina') {
      this.projects.sort(function(a,b) {
        return parseInt(a[prop]) < parseInt(b[prop])? -1 : 1;
      })
    }
  },
  created() {
    db.collection('projects').onSnapshot(res => {
      const changes = res.docChanges();

      changes.forEach(change => {
        if (change.type === 'added') {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id
          })
        }
      })
    })
  }
}
</script>

<style>

.project.complete{
  border-left: 4px solid #3cd1c2;
}
.project.ongoing{
  border-left: 4px solid #ffaa2c;
}
.project.edit{
  border-left: 4px solid #ffaa2c;
}
.project.overdue{
  border-left: 4px solid #f83e70;
}
.v-chip.complete{
  background: #3cd1c2;
}
.v-chip.ongoing{
  background: #ffaa2c;
}

.v-chip.edit{
  background: #ffaa2c;
}
.v-chip.overdue{
  background: #f83e70;
}

</style>
